import { _decorator, Component, Node, Enum, RigidBody2D } from 'cc';
import { EnumHole } from '../../framework/enum';
const { ccclass, property } = _decorator;

@ccclass('hole')
export class hole extends Component {

    @property({ type: Enum(EnumHole) })
    type: EnumHole = EnumHole.normal;

    onLoad() {
        let rb2d : RigidBody2D = this.node.getComponent(RigidBody2D)
        rb2d.enabledContactListener = true;

        
    }
}

